.section__jalurseleksi {
    background-image: url("../img/bg-pattern.png");
    background-size: cover;
    padding: unset;
    @media (min-width: 640px) {
        padding: 80px 0;
    }
    .title-content {
        font-size: 27px;
        color: #333d47;
        font-weight: 900;
        margin-top: 0;
        margin-bottom: 10px;
        @media (min-width: 640px) {
            font-size: 50px;
        }
        span {
            color: #c03f94;
        }
    }
    .wrapper__jalurseleksi {
        padding-top: 10px;
        @media (min-width: 640px) {
            padding-top: 50px;
        }
        &--card {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            // background-color: rgba($color: #fafafa, $alpha: 1);
        }
        .card-seleksi {
            margin: 20px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            transition: all 0.3s ease-in-out;
            @media(min-width: 640px) {
                margin: unset;
            }
            width: 350px;
            height: 350px;
            border-radius: 10px;
            background: #fff;
            position: relative;
            border: 1px solid #e8e8e8;
        box-shadow: 0 26px 32px rgba(26, 36, 72, .05);

            &:hover {
                transform: translateY(-25px);
            }
            .icon {
                left: 0;
                left: 0;
                position: absolute;
                top: -1px;
                background-color: #f7991e;
                height: 44px;
                width: 100%;
                color: #fff;
                padding: 0 9px;
                vertical-align: middle;
                display: flex;
                justify-content: center;
                align-items: center;
                svg {
                    padding-right: 7px;
                }
            }
            .desc {
                padding: 20px;
                font-size: 19px;
                line-height: 25px;
                letter-spacing: 0.4px;
                text-align: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
                
                @media(min-width:640px) {
                    min-height: 160px;
                }
            }
            .btn--primary {
                margin: 0;
            }
        }
        // position: relative;
        // width: 100%;
        // height: 100%;
        // background-color: rgba($color: #fff, $alpha: .4);
    }
}
